import React, { useState } from "react";
import brandlogo from "../commonComponents/images/logo-1.png";
import { Link, useNavigate } from "react-router-dom";
import { Container } from "react-bootstrap";
import validator from "email-validator";
import { ToastContainer, toast } from "react-toastify";
import { forgotUserPassword } from "../../store/actions/user.action.js";

import Loader from "../commonComponents/Loader/Loader";
import { useTitle } from '../../helpers/custom-hooks.js';

function PasswordForgot() {
	const [email, setEmail] = useState("");
	const [isLoading, setisLoading] = useState(false);
	const navigate = useNavigate();

	useTitle('Forgot Password');

	const handleFormSubmit = async (e) => {
		e.preventDefault();
		if (!validator.validate(email)) return toast.error("Invalid e-mail");

		setisLoading(true);
		const response = await forgotUserPassword({ email });
		setisLoading(false);

		if (response.status === 200) {
			toast.success(response.data?.message);
			setTimeout(() => {
				navigate("/login");
			}, 800);
		} else {
			toast.error(response?.message?.message);
		}
	};

	return (
		<div className="row m-0">
			<Container className="col-11">
				<div className="d-flex flex-center flex-column flex-column-fluid pt-20">
					<Link to="/login" className="mb-12">
						<img alt="brand-logo" src={brandlogo} className="h-40px" />
					</Link>
				</div>
				<div className="w-lg-500px bg-body card rounded shadow-sm p-10 p-lg-15 mx-auto">
					<form
						onSubmit={handleFormSubmit}
						className="form w-100"
						noValidate="noValidate"
						id="kt_password_reset_form"
					>
						<div className="text-center mb-10">
							<h1 className="text-dark mb-3">Forgot Password ?</h1>
						</div>
						<div className="text-gray-600 fw-bold fs-4 mb-3 ">
							Enter your email to reset your password:
						</div>
						<div className="fv-row mb-10">
							<input
								className="form-control form-control-solid"
								type="email"
								placeholder="example@email.com"
								name="email"
								value={email}
								onChange={(e) => setEmail(e.target.value)}
							/>
						</div>
						<div className="d-flex flex-wrap justify-content-center pb-lg-0">
							<Link
								to="/login"
								className="btn btn-lg btn-light-primary fw-bolder  me-4"
								hidden={isLoading}
							>
								Cancel
							</Link>
							<button
								type="submit"
								id="kt_password_reset_submit"
								className="btn btn-lg btn-primary fw-bolder"
								disabled={isLoading}
							>
								<span className="indicator-label">Submit</span>
								<span className="indicator-progress">
									<Loader text="Lodding" />
									<span className="spinner-border spinner-border-sm align-middle ms-2"></span>
								</span>
							</button>
						</div>
					</form>
				</div>
			</Container>
			<ToastContainer />
		</div>
	);
};

export default PasswordForgot;
